.floating_nav {
  display: none;
}
.floating_nav a {
  padding: 0.8rem;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--color-light);
  font-size: 0.7rem;
}
.floating_nav a:hover {
  background: rgba(0, 0, 0, 0.3);
}
.floating_nav a.active {
  background: var(--color-secondary);
  color: var(--color-white);
}

@media screen and (max-width: 1024px) {
  .floating_nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    padding: 0.5rem 1.7rem;
    z-index: 22;
    position: fixed;
    display: block;
    left: 50%;
    transform: translateX(-50%);
    bottom: 1.8rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
  }
}
@media screen and (max-width: 600px) {
  .floating_nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    padding: 0.5rem 1.7rem;
    z-index: 22;
    position: fixed;
    display: block;
    left: 40%;
    transform: translateX(-40%);
    bottom: 2rem;
    display: flex;
    gap: 0.7rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
  }
}
