nav {
  width: 100% !important;
  height: 5rem;
  display: grid;
  place-items: center;
  background: #0a0a0a;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  transition: var(--transition);
}

.nav_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav_logo {
  width: 2.5rem;
  border-radius: 50%;
  overflow: hidden;
}
.nav_menu {
  display: flex;
  gap: 3.5rem;
}
#theme_icon {
  background: transparent;
  color: black;
  font-size: 2rem;
  cursor: pointer;
}

@media screen and (max-width: 1024px) {
  .nav_menu {
    display: none;
  }
}
@media screen and (max-width: 600px) {
  .hidden_nav {
    display: none;
  }
}
