.skills_container {
  width: 100vw;
  display: flex;
  justify-content: center;
}

.skills_cards {
  margin: 4rem 0 1.5rem;
  width: 95%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}
.card.skills_card {
  text-align: center;
  background: #1c1f2d;
  padding: 1.5rem;
  border-radius: var(--border-radius-2);
  cursor: pointer;
}
.skills_card_icon {
  font-size: 2.5rem;
  color: var(--color-white);
  border-radius: var(--border-radius-3);
  background: var(--color-secondary);
  width: fit-content;
  margin-inline: auto;
  padding: 0.6rem;
  display: flex;
  align-items: center;
  transition: var(--transition);
}
.card.skills_card:hover .skills_card_icon {
  background: var(--color-white);
  color: var(--color-secondary);
}
.card.skills_card h4 {
  margin: 1rem 0;
  color: var(--color-white);
}

@media screen and (max-width: 1024px) {
  .skills_cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
